import React, { Component } from 'react';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import Container from 'react-bootstrap/Container';
import './mynavbar.css';

export default class MyNavbar extends Component {
    constructor(props){
        super(props);
        /* activePage = 0 = Home, */
        /* activePage = 1 = About, */
        /* activePage = 2 = Contact, */                
        this.state = {
            activePage: 0
        }
        this.loadTab = this.loadTab.bind(this);
    }

    isActiveTab = (index) => {return index === this.state.activePage}
    setActiveTab = (index) => {this.setState({activePage: index})}

    loadTab = (index, e) => {
        e.preventDefault();
        this.setActiveTab(index);
        // console.log(index);
        // console.log(e);
    }

    render(){
        return(
                <Container>
                    <Navbar className="navbarColor" fixed="bottom">
                        <Nav className="mynavbar">
                            <Nav.Link className="text-center" href="#" active={this.isActiveTab(0)} onClick={(e) => this.loadTab(0, e)}>Home</Nav.Link>
                            <Nav.Link className="text-center" href="#" active={this.isActiveTab(1)} onClick={(e) => this.loadTab(1, e)}>About</Nav.Link>
                            <Nav.Link className="text-center" href="#" active={this.isActiveTab(2)} onClick={(e) => this.loadTab(2, e)}>Contact</Nav.Link>
                        </Nav>
                    </Navbar>
                </Container>
        )
    }
}